import React, { useState } from 'react'
import { useGlobalContext } from '../context/globalContext'
import showToast from '../services/toasterService'
import BackIcon from '../assets/leftArrow-black.svg'
import InputLabel from "@mui/material/InputLabel"
import FormControl from "@mui/material/FormControl"
import {
  OutlinedInput,
  Button,
} from "@mui/material"
import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded"

const ResetPassword = () => {
  const { setIsResetpassword, resetPassword } = useGlobalContext()
  const [resetEmail, setResetEmail] = useState('')
  const [isLoading, setIsLoading] = useState(false)

  const handleResetPassword = () => {
    const emailCheck =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/

    console.log('email', resetEmail)
    if (resetEmail && emailCheck.test(resetEmail)) {
      console.log('email', resetEmail)
      resetPassword({
        body: {
          email: resetEmail,
        },
        setData: (data) => {
          setResetEmail('')
          setIsResetpassword(false)
          showToast('warning', 'Email Sent Successfully')
        },
        setLoading: setIsLoading,
        setError: (error) => {
          if (error) {
           
              console.log(error)
              showToast('error', error.response)
            
          }
        },
      })
    } else {
      showToast('error', 'Email is incorrect')
    }
  }

  return (
    <>
      <div className='flex justify-center flex-col p-4'>
        <div
          className='flex text-center'
          onClick={() => setIsResetpassword(false)}
        >
          <img src={BackIcon} alt='back' height={16} width={16} />
          <h4>Back</h4>
        </div>
        <div className='px-10'>
          <div className='w-full h-10 bg-transparent' />
          <div className=' text-center'>
            <h3 className='text-2xl font-semibold'>Reset Password</h3>
          </div>


          <FormControl variant="outlined" fullWidth={true}>
              <InputLabel htmlFor="forEmail">Enter Email</InputLabel>
              <OutlinedInput
                id="forEmail"
                type="text"
                fullWidth={true}
                value={resetEmail}
                onChange={(e) => setResetEmail(e.target.value)}
                label="Enter Email"
              />
            </FormControl>

            <Button
                variant="contained"
                endIcon={<ArrowForwardRoundedIcon />}
                fullWidth={true}
                onClick={handleResetPassword}
              >
                LOGIN
              </Button>
            
        

          {isLoading ? (
            <div className='btn btn-primary'>Loading</div>
          ) : (
            <div
              className='btn btn-primary w-full'
              onClick={handleResetPassword}
            >
              <p>Send Email</p>
              <svg
                xmlns='http://www.w3.org/2000/svg'
                width='16'
                height='16'
                fill='currentColor'
                class='bi bi-arrow-right'
                viewBox='0 0 16 16'
              >
                <path
                  fillRule='evenodd'
                  d='M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z'
                />
              </svg>
            </div>
          )}
        </div>
      </div>
    </>
  )
}

export default ResetPassword
