import * as React from "react"
import { Box, Container, Grid, Typography } from "@mui/material"
import Card from "@mui/material/Card"
// import CardActions from "@mui/material/CardActions"
import CardContent from "@mui/material/CardContent"
import CardMedia from "@mui/material/CardMedia"
// import Button from "@mui/material/Button"
import SearchRoundedIcon from "@mui/icons-material/SearchRounded"
import OutlinedInput from "@mui/material/OutlinedInput"
// import InputLabel from "@mui/material/InputLabel"
import Select from "@mui/material/Select"
import MenuItem from "@mui/material/MenuItem"
// import FormHelperText from "@mui/material/FormHelperText"
import InputAdornment from "@mui/material/InputAdornment"
import FormControl from "@mui/material/FormControl"
import IconButton from "@mui/material/IconButton"
import { makeStyles } from "@material-ui/core"
import clsx from "clsx"

import { useNavigate } from "react-router-dom"
import { useGlobalContext } from "../../context/globalContext"

const styles = makeStyles((theme) => ({
  root: {
    // height: "auto",
    paddingBottom: "100%",
    display: "block",
    position: "relative",
    // position:"relative",
    // top:0
    // maxHeight:0,
    // minHeight:"372px"
  },
  media: {
    position: "absolute",
    // position: "absolute",
    left: 0,
    top: 0,
    height: "100%", //auto
    // maxWidth:"100%"
    objectFit: "scale-down",
  },
}))
export default function CourseCard() {
  const navigate = useNavigate()
  const classes = styles()
  const [topic, setTopic] = React.useState("")
  const [type, setType] = React.useState("")

  const handleTopic = (event) => {
    setTopic(event.target.value)
  }

  const handleType = (event) => {
    setType(event.target.value)
  }

  const { allCourses, isCourseLoading } = useGlobalContext()

  const handleCardOpen = async (data) => {
    console.log("data", data)
    navigate(`/experts-profile/${data._id}`)
    // await selectedCourse(data)
  }
  return (
    <>
      <Container maxWidth="lg" sx={{ mt: 8 }}>
        <Typography variant="h4" sx={{ fontWeight: "bold" }}>
          Educators
        </Typography>
        <Grid container direction={"row"} spacing={3} sx={{my:2}}>
          <Grid item>
            <FormControl sx={{ width: "25ch" }} variant="outlined">
              <OutlinedInput
                id="outlined-adornment-password"
                type="text"
                value=""
                //    sx={{borderRadius:'30px'}}
                size="small"
                placeholder="Search here"
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      //   onClick={handleClickShowPassword}
                      //   onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      <SearchRoundedIcon />
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
          </Grid>

          <Grid item>
            <FormControl sx={{ minWidth: 120 }}>
              <Select
                value={topic}
                onChange={handleTopic}
                displayEmpty
                size="small"
                inputProps={{ "aria-label": "Without label" }}
              >
                <MenuItem value="" disabled={true}>
                  <em>Select Topic</em>
                </MenuItem>
                <MenuItem value={10}>Topic 1</MenuItem>
                <MenuItem value={20}>Topic 2</MenuItem>
                <MenuItem value={30}>Topic 3</MenuItem>
              </Select>
            </FormControl>
          </Grid>

          <Grid item>
            <FormControl sx={{ minWidth: 120 }}>
              <Select
                value={type}
                onChange={handleType}
                displayEmpty
                size="small"
                inputProps={{ "aria-label": "Without label" }}
              >
                <MenuItem value="" disabled={true}>
                  <em>Select Type</em>
                </MenuItem>
                <MenuItem value={10}>Type 1</MenuItem>
                <MenuItem value={20}>Type 2</MenuItem>
                <MenuItem value={30}>Type 3</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Grid sx={{ flexGrow: 1 }} container spacing={8}>
          {isCourseLoading ? (
            <Typography>Loading...</Typography>
          ) : (
            allCourses.map((data) => (
              <Grid
                item
                xs={4}
                
                key={data.id}
                
              >
                <Box sx={{ maxWidth: 350 }}>
                  <Card onClick={() => handleCardOpen(data)} variant="outlined" sx={{ px: 2, pt: 2, "&:hover": {
                    boxShadow: 'rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px',
                    transition: 'all 0.3s ease-in-out',
                  } }}>
                    <Grid xs={12}>
                      <div className={classes.root}>
                        <CardMedia
                          component={"img"}
                          className={clsx(classes.media)}
                          src={"https://i.pravatar.cc/300?img=32"}
                          sx={{ borderRadius: "6%" }}
                        />
                      </div>
                    </Grid>
                    <CardContent>
                      <Typography sx={{ fontWeight: "medium" }}>
                        {data.author}
                      </Typography>
                      <Typography>{data.courseName}</Typography>
                    </CardContent>
                  </Card>
                </Box>
              </Grid>
            ))
          )}
        </Grid>
      </Container>
    </>
  )
}
