import React, { useContext, useState, useReducer, useEffect } from 'react'
import AuthObject from '../utils/AuthStorage'
import Auth from '../states/Auth'
import axios from 'axios'
import useLocalStorage from '../hooks/useLocalStorage'
import { BASE_URL } from "../constants";


const GlobalContext = React.createContext()

export function useGlobalContext() {
  return useContext(GlobalContext)
}

export function GlobalContextProvider({ children }) {
  
  const [authState, authDispatch] = useReducer(Auth.reducer, Auth.initialState)

  const [isSigninClicked, setIsSigninClicked] = useState()
  const [isResetpassword, setIsResetpassword] = useState(false)
  const [selectedEmail, setSelectedEmail] = useLocalStorage('email', '')
  const [user, setUser] = useState()
  // const [loginLoading, setLoginLoading] = useState(false)
  const [isOpen, setOpen] = useState(false)

  const [allCourses, setAllCourses] = useState([]);
  const [openedCourse, setOpenedCourse] = useState();
  const [isCourseLoading, setIsCourseLoading] = useState(true);


  const handlePopup = (type) => {
    if (type === 'signIn') {
      setIsSigninClicked(true)
    } else {
      console.log(type)
      setIsSigninClicked(false)
    }
    setOpen(true)
  }

  // const selectedCourse = async (data) => {
  //   console.log('opened course data', data)
  //   return await axios
  //     .post(`/api/get_courses`, data)
  //     .then(({ data }) => {
  //       setOpenedCourse(data)
        
  //       console.log('course name ------------------', data.data.courseName)
  //       return data
  //     })
  //     .catch((error) => {
  //       console.error(error)
     
  //       return error
  //     })

  // }


  const clearState = () => {
    Auth.Actions.resetState(authDispatch)
  }

  const resetPassword = async ({ body, setData, setLoading, setError }) => {
    setLoading(true)
    setError(false)
    return await axios
      .post(`/api/reset-password`, body)
      .then(({ data }) => {
        setData(data)
        setLoading(false)
        console.log('ResetPassword Fetch ------------------')
        return data
      })
      .catch((error) => {
        console.error(error)
        setLoading(false)
        setError(error)
        return error
      })
  }

  const userData = (data) => {
    console.log('user data', data)
  }

  const getUserDetails = async () =>{
    console.log('get user details')
    return await axios
      .post(`/api/getUser`, { email: selectedEmail })
      .then(({ data }) => {
       console.log(data)
        return data
      })
      .catch((error) => {
       console.error(error)
        return error
      })
  }

  const getAllCourses = async () => {
    setIsCourseLoading(true);
    return axios
      .get(`${BASE_URL}/api/get_all_courses`)
      .then(({ data }) => {
        console.log("all course data------------------", data.data);
        setAllCourses(data.data);
        setIsCourseLoading(false);
        return data.data;
      })
      .catch((error) => {
        setIsCourseLoading(false);
        return error;
      });
  }

  useEffect(() => {
    const { token } = AuthObject.getToken()
    Auth.Actions.setAuthStatus(token ? true : false, authDispatch)
    if (token) {
      getUserDetails()
    }

    getAllCourses()
    


    // Auth.Actions.setAuthStatus(
    //   localStorage.getItem("token") ? true : false,
    //   authDispatch
    // );
    // Auth.Actions.setSubscriberId(
    //   localStorage.getItem("id") ? localStorage.getItem("id") : null,
    //   authDispatch
    // );
  }, [])

  return (
    <GlobalContext.Provider
      value={{
        setAuthStatus: (status) =>
        Auth.Actions.setAuthStatus(status, authDispatch),
        ...authState,
        handlePopup,
        clearState,
        isOpen,
        isSigninClicked,
        setOpen,
        setIsResetpassword,
        isResetpassword,
        resetPassword,
        setUser,
        user,
        setSelectedEmail,
        selectedEmail,
        getUserDetails,
        // selectedCourse,
        openedCourse,
        setOpenedCourse,
        userData,
        allCourses,
        isCourseLoading,
        getAllCourses
      }}
    >
      {children}
    </GlobalContext.Provider>
  )
}

export default GlobalContextProvider
