import React, { useEffect, useState } from "react"
import axios from "axios"
import { Avatar, Box, Button, CardMedia, Grid, Menu, MenuItem, Paper, Typography } from "@mui/material"
import { Container } from "@mui/system"
import MoreVertRoundedIcon from "@mui/icons-material/MoreVertRounded"
import FavoriteBorderRoundedIcon from "@mui/icons-material/FavoriteBorderRounded"

const ExpertsProfile = () => {
  const [opendCourse, setOpendCourse] = useState()
  const [anchorEl, setAnchorEl] = React.useState(null);

  useEffect(() => {
    const url = window.location.href
    const parts = url.split("/")
    const result = parts[parts.length - 1]
    const callCourse = { _id: result }
    selectedCourse(callCourse)
  }, [])

  const selectedCourse = async (data) => {
    return await axios
      .post(`/api/get_courses`, data)
      .then(({ data }) => {
        console.log("course name ------------------", data)
        setOpendCourse(data)
        return data
      })
      .catch((error) => {
        console.error(error)
        return error
      })
  }



  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <Box width={"100%"} sx={{ height: "150px", backgroundImage: `url(https://api.lorem.space/image/shoes?w=1500&h=150)`,
    backgroundSize: "cover", }} >
      </Box>
      <Container>
        <Grid container>
          <Grid container xs={6}>
            <Grid item xs={4}>
              {/* <Avatar
                sx={{
                  width: "150px",
                  height: "150px",
                  marginTop: "-40px",
                  border: "4px solid white",
                }}
              ></Avatar> */}
              <CardMedia
                          component={"img"}
                          
                          src={"https://i.pravatar.cc/200?img=33"}
                          sx={{ borderRadius: "50%",width: "180px",
                          height: "180px",
                          marginTop: "-40px",
                          border: "4px solid white", }}
                        />
            </Grid>
            <Grid xs={8} container marginTop={"20px"} direction={"column"}>
              <Grid item>
                <Typography variant="h5" sx={{ fontWeight: "medium" }}>
                  Ravi Patel
                </Typography>
              </Grid>
              <Grid item>
                <Typography>Software developer @ ICICI Lombard</Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid
            container
            direction={"row-reverse"}
            xs={6}
            spacing={1}
            align="right"
            marginY={"auto"}
          >
            <Grid item>
              <Button
                variant="outlined"
                onClick={handleMenu}
                sx={{
                  px: 0,
                  display: "inline-block",
                  minHeight: 0,
                  minWidth: "40px",
                 
                }}
              >
                <MoreVertRoundedIcon />
                </Button>
                <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'left',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <MenuItem onClick={handleClose}>Profile</MenuItem>
                <MenuItem onClick={handleClose}>My account</MenuItem>
              </Menu>
              
            </Grid>
            <Grid item>
              <Button
                variant="outlined"
                sx={{
                  px: 0,
                  display: "inline-block",
                  minHeight: 0,
                  minWidth: "40px",
                
                }}
              >
                <FavoriteBorderRoundedIcon />
              </Button>
            </Grid>
            
          </Grid>
        </Grid>
      </Container>
    </>
  )
}

export default ExpertsProfile
